<template>
  <div class="app-container">

  

      <!-- <el-container>
        <el-header>
          <el-input placeholder="Filter keyword" v-model="filterText" style="margin-bottom:30px;"></el-input>
        </el-header>
      </el-container> -->
      <el-container direction="horizontal"  
        v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading">
        <el-container direction="vertical"  style="height:700px;width:30%;">
          <el-header style="height:100px">
            <el-row>
              <el-select v-model="server_selected" placeholder="请选择玩家所在的区服">
                <el-option
                    v-for="item in servers"
                    :key="item.server_ip"
                    :label="item.server_name"
                    :value="item.server_id">
                    <span style="float: left">{{ item.server_name }}</span>
                    <span style="float: right; color: #8492a6; font-size: 13px">{{ item.server_ip }}</span>
                </el-option>
              </el-select>
            </el-row>
            <el-row :gutter="10" type="flex" style="margin-top:10px">
              <el-col style="width:70%">
                <el-input placeholder="输入要查询的玩家名字" v-model="roleName"></el-input>
              </el-col>
              <el-col style="width:30%">
                <el-button type="primary" @click="queryPlayerInfo" >确定</el-button>
              </el-col>
            </el-row>

          </el-header>
          <el-aside>
            <el-tree  class="filter-tree" :data="data2" :props="defaultProps" @node-click="handleNodeClick" style="height:580px"></el-tree>
          </el-aside>
        </el-container>
        <el-container direction="vertical" style="height:700px;width:70%"> 
          <el-header>
              <el-input placeholder="Filter keyword" v-model="filterText" style="margin-bottom:30px;width:300px;"></el-input>
          </el-header>  
          <el-main>      
            <el-table
              :data="tableData"
              border
              stripe
              height="600px"
              style="width: 100%">
              <el-table-column 
                v-for="col in cols"
                :key="col.label"
                :prop="col.prop" :label="col.label" >
              </el-table-column>
              <!-- <el-table-column
                prop="date"
                label="日期"
                width="180">
              </el-table-column> -->
            </el-table>
          </el-main>
        </el-container>

      </el-container>

  </div>

 
</template>

<script>
import { serversPower } from '@/api/user';
import { playerInfo } from '@/api/player';
export default {
  watch: {
    filterText: function(val, oldVal) {
      console.log(val);
      this.tableData = this.tableData.filter(p => ~p.Key.indexOf(val));
    }
  },
  methods: {
    mapRecordsData(dom, records) {
      if (records !== null) {
        for (let i = 0; i < records.length; i++) {
          const element = records[i];
          dom.push({
            id: i,
            type: 1,
            label: element.RecName,
            data: { Rows: element.Rows, Cols: element.Columns },
            children: []
          });
        }
      }
    },
    mapChildData(dom, childs) {
      if (childs !== null) {
        for (let i = 0; i < childs.length; i++) {
          const element = childs[i];
          dom.push({
            label: element.Script,
            children: [{
              id: i,
              type: 0,
              data: element.Attrs.Attr,
              label: '属性'
            },
            {
              id: i,
              label: '表格',
              children: []
            },
            {
              id: i,
              type: 2,
              label: '子对象',
              children: []
            }
            ]
          });
          this.mapChildData(dom[i].children[2].children, element.Childs);
          this.mapRecordsData(dom[i].children[1].children, element.Records.Records);
        }
      }
    },
    queryPlayerInfo() {
      const data = {
        game_id: 1,
        server_id: this.server_selected,
        role_name: this.roleName
      };
      this.loading = true;
      playerInfo(data).then(resposne => {
        this.mapChildData(this.Childs_dom, resposne.Data.Childs);
        this.mapRecordsData(this.Records_dom, resposne.Data.Records.Records);
        var tree_data = [];
        tree_data.push({
          id: 0,
          type: 0,

          data: resposne.Data.Attrs.Attr,
          label: '属性'
        }, {
          id: 1,

          label: '表格',
          children: this.Records_dom
        }, {
          id: 2,
          type: 2,
          label: '子对象',
          children: this.Childs_dom
        });
        this.data2 = tree_data;
        console.log(this.data2);
        this.loading = false;
      });
    },
    getServersList(username) {
      serversPower(username).then(response => {
        this.servers = response.Data.servers;
      });
    },
    handleNodeClick(data) {
      this.tableData = [];
      this.cols = [];
      // 属性
      if (data.type === 0) {
        this.tableData = data.data;
        this.cols.push(
          { prop: 'Key', label: '属性' },
          { prop: 'Val', label: '值' }
        );
        console.log(this.cols);
      }
      // 表格
      if (data.type === 1) {
        var cols_arr = data.data.Cols.ColType;
        var rows_arr = data.data.Rows;
        var table_data = [];
        for (let col_index = 0; col_index < cols_arr.length; col_index++) {
          var temp_key = 'Key' + col_index.toString();
          table_data[temp_key] = [];

          this.cols.push({
            prop: temp_key,
            label: cols_arr[col_index].toString()
          });
        }

        for (let row_index = 0; row_index < rows_arr.length; row_index++) {
          const element = rows_arr[row_index].Values;
          var every_arr = {};
          for (let a = 0; a < cols_arr.length; a++) {
            var temp2_key = 'Key' + a.toString();
            every_arr[temp2_key] = element[a].Val;
          }
          table_data.push(every_arr);
        }

        this.tableData = table_data;
      }
      this.filter_data = this.tableData;
    }
  },

  created() {
    this.getServersList(this.$store.getters.name);
  },
  data() {
    return {
      // filter_data,
      loading: false,
      server_selected: '',
      servers: [],
      tableData: [],
      cols: [],
      data2: [],
      filterText: '',
      roleName: '',
      defaultProps: {
        children: 'children',
        label: 'label'
      },
      Childs_dom: [],
      Attrs_dom: [],
      Records_dom: []

    };
  }
};
</script>




